import React,{useState} from 'react';
import { Link } from 'react-router-dom';
import "./Login.css"
import FullPageLoader from "../fullpageloader/fullPageLoader";
const config = require('../../helpers/config.json');


const Login = () => {
  
    return (
        <>
            <div className="container-fluid  py-2">
                <div className="row ">
                    <div className="col-lg-4 col-md-12" />
                    <div className="col-lg-4 col-md-12 mt-4">
                        <div className="wrapper py-4" >
                            <div id="formContent" className='py-3' style={{boxShadow:"lightgrey 1px 9px 9px 6px"}}>

                                <div className="py-4 first d-flex justify-content-center">
                                    {/* <img src="data:image/png;base64," height="40px" width="40px" id="icon" alt="User Icon" className="mt-2" /> */}
                                    <h2 className="ml-2 font-weight-bold"> REQUESTER SIGN IN</h2>
                                </div>

                                <div >
                                    <label style={{display:"flex",position:"relative",left:"34px",top:"20px",fontSize:"17px"}} htmlFor="">EMAIL</label><br />
                                    <input type="text"  id="login" className="fadeIn second  mb-3 border border-dark rounded-0" name="login" placeholder="EMAIL" />
                                    <label style={{display:"flex",position:"relative",left:"34px",top:"20px",fontSize:"17px"}} htmlFor="">PASSWORD</label><br />
                                    <input
                                    
                                    type="PASSWORD"  id="password" className="fadeIn third border border-dark rounded-0" name="login" placeholder="password" />
                                    <Link to="/dashboard"><input type="submit"  className="my-5 bg-white text-dark border border-dark rounded-0 text-center py-3" defaultValue="SIGN IN" style={{cursor:"pointer"}} /></Link>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-4 col-md-12" />
                </div>
            </div>
        </>
    );
}

export default Login; 